import React from 'react';
import { Link } from 'react-router-dom';

const StoriesList = ({ stories }) => (
    <>
    {stories.map((story, key) => (
        <div key={key} className="story-list-item">
            <Link className="story-list-item" to={`/story/${story.name}`}>
            <h3>{story.title}</h3>
        </Link>
        <p className="story-list-item">{story.content[0].substring(0, 150)}...</p>
        </div>
    ))}
    </>
);

export default StoriesList;