import React from 'react';

const UpvotesSection = ({ storyName, upvotes, setStoryInfo }) => {
    const upvoteStory = async () => {
        const result = await fetch(`/api/stories/${storyName}/upvote`, {
            method: 'post',
        });
        const body = await result.json();
        setStoryInfo(body);
    }
    return (
        <div id="upvotes-section">
            <button onClick={() => upvoteStory()}>Add Upvote</button>
            <p>This post has been upvoted {upvotes} times</p>
        </div>
    );
}

export default UpvotesSection;