import React, { useState, useEffect } from 'react';
import StoriesList from '../components/StoriesList';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';
import NotFoundPage from './NotFoundPage';
import storyContent from './story-content';

const StoryPage = (props) => {
    const name = props.match.params.name;
    const ln = props.location;
    const story = storyContent.find(story => story.name === name);

    const [storyInfo, setStoryInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/stories/${name}`);
            const body = await result.json();
            setStoryInfo(body);
        }
        fetchData();
    }, [name]);

    if (!story) return (
        <><NotFoundPage location={ln}/></>
    );

    const otherStories = storyContent.filter(story => story.name !== name);

    return (
        <>
        <h1>{story.title}</h1>
        <UpvotesSection storyName={name} upvotes={storyInfo.upvotes} setStoryInfo={setStoryInfo} />
        {story.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}
        <AddCommentForm storyName={name} setStoryInfo={setStoryInfo} />
        <CommentsList comments={storyInfo.comments} />
        
        <h3>Other Stories:</h3>
        <StoriesList stories={otherStories} />
        </>
    );
}

export default StoryPage;